/**
 * ÈÖçÈ°çÂÑÄË°®ÊùøË¶ñÂúñ
 * 
 * ÂÆåÊï¥ÁöÑÈÖçÈ°çÁÆ°ÁêÜÁïåÈù¢ÔºåÂåÖÂê´Ôºö
 * 1. ÈÖçÈ°ç‰ΩøÁî®Ê¶ÇË¶Ω
 * 2. Ë∂®Âã¢ÂúñË°®
 * 3. ÈáçÁΩÆÂÄíË®àÊôÇ
 * 4. Ê≠∑Âè≤Ë®òÈåÑ
 * 5. ÂçáÁ¥öÂºïÂ∞é
 */

import { Component, OnInit, OnDestroy, signal, computed, inject, ChangeDetectionStrategy } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Router } from '@angular/router';
import { QuotaService, QuotaInfo, QuotaSummary } from '../services/quota.service';
import { AuthService } from '../core/auth.service';
import { ElectronIpcService } from '../electron-ipc.service';

interface QuotaHistoryItem {
  date: string;
  quota_type: string;
  used: number;
  limit: number;
}

interface TrendData {
  labels: string[];
  datasets: {
    name: string;
    data: number[];
    color: string;
  }[];
}

@Component({
  selector: 'app-quota-dashboard-view',
  standalone: true,
  imports: [CommonModule, RouterModule],
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
    <div class="quota-dashboard">
      <!-- È†ÅÈù¢È†≠ÈÉ® -->
      <header class="page-header">
        <div class="header-left">
          <h1>ÈÖçÈ°çÁÆ°ÁêÜ</h1>
          <p>Êü•ÁúãÂíåÁÆ°ÁêÜÊÇ®ÁöÑË≥áÊ∫êÈÖçÈ°ç</p>
        </div>
        <div class="header-right">
          <div class="tier-badge" [style.background]="tierGradient()">
            <span class="tier-icon">{{ tierIcon() }}</span>
            <span class="tier-name">{{ tierName() }}</span>
          </div>
          <button class="upgrade-btn" (click)="goToUpgrade()">
            ÂçáÁ¥öÊñπÊ°à
          </button>
        </div>
      </header>
      
      <!-- ÈáçÁΩÆÂÄíË®àÊôÇ -->
      <div class="reset-countdown" *ngIf="nextResetTime()">
        <div class="countdown-icon">‚è∞</div>
        <div class="countdown-info">
          <span class="countdown-label">ÊØèÊó•ÈÖçÈ°çÈáçÁΩÆ</span>
          <span class="countdown-time">{{ nextResetTime() }}</span>
        </div>
      </div>
      
      <!-- ÈÖçÈ°çÂç°ÁâáÁ∂≤Ê†º -->
      <div class="quota-grid">
        <div class="quota-card" *ngFor="let quota of displayQuotas()"
             [class.warning]="quota.status === 'warning' || quota.status === 'critical'"
             [class.exceeded]="quota.status === 'exceeded'"
             [class.unlimited]="quota.unlimited">
          <div class="card-header">
            <div class="quota-icon">{{ getQuotaIcon(quota.type) }}</div>
            <div class="quota-info">
              <h3>{{ quotaService.getQuotaDisplayName(quota.type) }}</h3>
              <span class="quota-status" [style.color]="getStatusColor(quota.status)">
                {{ getStatusText(quota.status) }}
              </span>
            </div>
          </div>
          
          <div class="card-body">
            <div class="quota-value" *ngIf="!quota.unlimited">
              <span class="used">{{ formatNumber(quota.used) }}</span>
              <span class="separator">/</span>
              <span class="limit">{{ formatNumber(quota.limit) }}</span>
            </div>
            <div class="quota-value unlimited" *ngIf="quota.unlimited">
              <span class="infinity">‚àû</span>
              <span class="used-text">Â∑≤‰ΩøÁî® {{ formatNumber(quota.used) }}</span>
            </div>
            
            <div class="progress-container" *ngIf="!quota.unlimited">
              <div class="progress-bar">
                <div class="progress-fill" 
                     [style.width.%]="quota.percentage"
                     [style.background]="getProgressGradient(quota.status)">
                </div>
              </div>
              <span class="percentage">{{ quota.percentage.toFixed(1) }}%</span>
            </div>
            
            <div class="quota-details">
              <span class="remaining" *ngIf="!quota.unlimited">
                Ââ©È§ò: {{ formatNumber(quota.remaining) }}
              </span>
              <span class="reset-hint" *ngIf="quota.resetAt">
                {{ formatResetTime(quota.resetAt) }} ÈáçÁΩÆ
              </span>
            </div>
          </div>
          
          <!-- Mini Ë∂®Âã¢Âúñ -->
          <div class="mini-trend" *ngIf="getTrendData(quota.type) as trend">
            <svg viewBox="0 0 100 30" preserveAspectRatio="none">
              <polyline
                [attr.points]="trend"
                fill="none"
                stroke="currentColor"
                stroke-width="2"
                stroke-linejoin="round"
              />
            </svg>
          </div>
        </div>
      </div>
      
      <!-- ‰ΩøÁî®Ë∂®Âã¢ÂúñË°® -->
      <section class="trend-section">
        <div class="section-header">
          <h2>‰ΩøÁî®Ë∂®Âã¢</h2>
          <div class="period-selector">
            <button 
              *ngFor="let period of periods" 
              [class.active]="selectedPeriod() === period.value"
              (click)="selectedPeriod.set(period.value)">
              {{ period.label }}
            </button>
          </div>
        </div>
        
        <div class="chart-container">
          <div class="chart-legend">
            <div class="legend-item" *ngFor="let dataset of trendData()?.datasets || []">
              <span class="legend-color" [style.background]="dataset.color"></span>
              <span class="legend-label">{{ dataset.name }}</span>
            </div>
          </div>
          
          <div class="chart-area">
            <svg viewBox="0 0 400 150" preserveAspectRatio="none" class="trend-chart">
              <!-- ËÉåÊôØÁ∂≤Ê†º -->
              <g class="grid">
                <line *ngFor="let i of [0,1,2,3,4]" 
                      [attr.y1]="i * 37.5" [attr.y2]="i * 37.5"
                      x1="0" x2="400" />
              </g>
              
              <!-- Ë∂®Âã¢Á∑ö -->
              <g *ngFor="let dataset of trendData()?.datasets || []; let idx = index">
                <polyline
                  [attr.points]="generateChartPoints(dataset.data)"
                  fill="none"
                  [attr.stroke]="dataset.color"
                  stroke-width="2"
                  stroke-linejoin="round"
                />
                <!-- Êï∏ÊìöÈªû -->
                <circle *ngFor="let point of dataset.data; let i = index"
                        [attr.cx]="(i / (dataset.data.length - 1 || 1)) * 400"
                        [attr.cy]="150 - (point / maxTrendValue()) * 150"
                        r="4"
                        [attr.fill]="dataset.color"
                        class="data-point"
                />
              </g>
            </svg>
            
            <!-- X Ëª∏Ê®ôÁ±§ -->
            <div class="chart-labels">
              <span *ngFor="let label of trendData()?.labels || []">{{ label }}</span>
            </div>
          </div>
        </div>
      </section>
      
      <!-- ‰ΩøÁî®Ê≠∑Âè≤ -->
      <section class="history-section">
        <div class="section-header">
          <h2>‰ΩøÁî®Ê≠∑Âè≤</h2>
          <button class="view-all-btn" (click)="showAllHistory = !showAllHistory">
            {{ showAllHistory ? 'Êî∂Ëµ∑' : 'Êü•ÁúãÂÖ®ÈÉ®' }}
          </button>
        </div>
        
        <div class="history-table">
          <div class="table-header">
            <span>Êó•Êúü</span>
            <span>È°ûÂûã</span>
            <span>‰ΩøÁî®Èáè</span>
            <span>ÈÖçÈ°ç</span>
            <span>‰ΩøÁî®Áéá</span>
          </div>
          <div class="table-body">
            <div class="table-row" *ngFor="let item of displayHistory()">
              <span class="date">{{ item.date }}</span>
              <span class="type">
                <span class="type-icon">{{ getQuotaIcon(item.quota_type) }}</span>
                {{ quotaService.getQuotaDisplayName(item.quota_type) }}
              </span>
              <span class="used">{{ formatNumber(item.used) }}</span>
              <span class="limit">{{ formatNumber(item.limit) }}</span>
              <span class="percentage" [class.warning]="getHistoryPercentage(item) > 80">
                {{ getHistoryPercentage(item).toFixed(1) }}%
              </span>
            </div>
            
            <div class="empty-history" *ngIf="(displayHistory() || []).length === 0">
              Êö´ÁÑ°Ê≠∑Âè≤Ë®òÈåÑ
            </div>
          </div>
        </div>
      </section>
      
      <!-- ÈÖçÈ°çÂëäË≠¶ -->
      <section class="alerts-section" *ngIf="(quotaService.alerts() || []).length > 0">
        <div class="section-header">
          <h2>ÈÖçÈ°çÂëäË≠¶</h2>
          <button class="clear-btn" (click)="clearAlerts()">ÂÖ®ÈÉ®Á¢∫Ë™ç</button>
        </div>
        
        <div class="alerts-list">
          <div class="alert-item" *ngFor="let alert of quotaService.alerts()"
               [class.acknowledged]="alert.acknowledged">
            <div class="alert-icon">‚ö†Ô∏è</div>
            <div class="alert-content">
              <span class="alert-type">{{ quotaService.getQuotaDisplayName(alert.quota_type) }}</span>
              <span class="alert-message">{{ alert.message }}</span>
            </div>
            <button class="ack-btn" *ngIf="!alert.acknowledged" 
                    (click)="acknowledgeAlert(alert.id)">
              Á¢∫Ë™ç
            </button>
          </div>
        </div>
      </section>
      
      <!-- ÂçáÁ¥öÂºïÂ∞é -->
      <section class="upgrade-section" *ngIf="showUpgradeHint()">
        <div class="upgrade-content">
          <div class="upgrade-icon">üöÄ</div>
          <div class="upgrade-text">
            <h3>Ëß£ÈéñÊõ¥Â§öÈÖçÈ°ç</h3>
            <p>ÂçáÁ¥öÂà∞Êõ¥È´òÁ≠âÁ¥öÔºåÁç≤ÂæóÊõ¥Â§öË≥áÊ∫êÈÖçÈ°çÂíåÈ´òÁ¥öÂäüËÉΩ</p>
          </div>
          <div class="upgrade-features">
            <span class="feature" *ngFor="let feature of upgradeFeatures">
              ‚úì {{ feature }}
            </span>
          </div>
        </div>
        <button class="upgrade-cta" (click)="goToUpgrade()">
          Êü•ÁúãÂçáÁ¥öÊñπÊ°à
          <span class="arrow">‚Üí</span>
        </button>
      </section>
    </div>
  `,
  styles: [`
    .quota-dashboard {
      padding: 24px;
      max-width: 1400px;
      margin: 0 auto;
    }
    
    /* È†ÅÈù¢È†≠ÈÉ® */
    .page-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 24px;
    }
    
    .header-left h1 {
      font-size: 24px;
      font-weight: 700;
      margin: 0 0 4px;
    }
    
    .header-left p {
      margin: 0;
      color: var(--text-secondary, #888);
      font-size: 14px;
    }
    
    .header-right {
      display: flex;
      align-items: center;
      gap: 16px;
    }
    
    .tier-badge {
      display: flex;
      align-items: center;
      gap: 8px;
      padding: 8px 16px;
      border-radius: 20px;
      color: white;
      font-weight: 600;
    }
    
    .tier-icon {
      font-size: 18px;
    }
    
    .upgrade-btn {
      padding: 10px 20px;
      background: linear-gradient(135deg, #8b5cf6, #6366f1);
      border: none;
      border-radius: 8px;
      color: white;
      font-weight: 600;
      cursor: pointer;
      transition: all 0.2s;
    }
    
    .upgrade-btn:hover {
      transform: translateY(-2px);
      box-shadow: 0 4px 12px rgba(139, 92, 246, 0.3);
    }
    
    /* ÈáçÁΩÆÂÄíË®àÊôÇ */
    .reset-countdown {
      display: flex;
      align-items: center;
      gap: 12px;
      padding: 12px 20px;
      background: rgba(59, 130, 246, 0.1);
      border: 1px solid rgba(59, 130, 246, 0.2);
      border-radius: 12px;
      margin-bottom: 24px;
    }
    
    .countdown-icon {
      font-size: 24px;
    }
    
    .countdown-label {
      display: block;
      font-size: 12px;
      color: var(--text-secondary, #888);
    }
    
    .countdown-time {
      font-size: 18px;
      font-weight: 700;
      color: var(--primary, #3b82f6);
    }
    
    /* ÈÖçÈ°çÂç°ÁâáÁ∂≤Ê†º */
    .quota-grid {
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
      gap: 20px;
      margin-bottom: 32px;
    }
    
    .quota-card {
      background: var(--bg-secondary, #1a1a1a);
      border: 1px solid var(--border-color, #333);
      border-radius: 16px;
      padding: 20px;
      transition: all 0.3s;
      position: relative;
      overflow: hidden;
    }
    
    .quota-card:hover {
      transform: translateY(-2px);
      box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);
    }
    
    .quota-card.warning {
      border-color: rgba(245, 158, 11, 0.5);
    }
    
    .quota-card.exceeded {
      border-color: rgba(239, 68, 68, 0.5);
      background: rgba(239, 68, 68, 0.05);
    }
    
    .quota-card.unlimited {
      border-color: rgba(139, 92, 246, 0.3);
    }
    
    .card-header {
      display: flex;
      align-items: flex-start;
      gap: 12px;
      margin-bottom: 16px;
    }
    
    .quota-icon {
      font-size: 32px;
    }
    
    .quota-info h3 {
      margin: 0;
      font-size: 16px;
      font-weight: 600;
    }
    
    .quota-status {
      font-size: 12px;
    }
    
    .card-body .quota-value {
      display: flex;
      align-items: baseline;
      gap: 4px;
      margin-bottom: 12px;
    }
    
    .quota-value .used {
      font-size: 32px;
      font-weight: 700;
    }
    
    .quota-value .separator {
      font-size: 18px;
      color: var(--text-secondary, #888);
    }
    
    .quota-value .limit {
      font-size: 18px;
      color: var(--text-secondary, #888);
    }
    
    .quota-value.unlimited {
      flex-direction: column;
      align-items: flex-start;
      gap: 4px;
    }
    
    .infinity {
      font-size: 36px;
      color: #8b5cf6;
    }
    
    .used-text {
      font-size: 14px;
      color: var(--text-secondary, #888);
    }
    
    .progress-container {
      display: flex;
      align-items: center;
      gap: 12px;
      margin-bottom: 12px;
    }
    
    .progress-bar {
      flex: 1;
      height: 8px;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 4px;
      overflow: hidden;
    }
    
    .progress-fill {
      height: 100%;
      border-radius: 4px;
      transition: width 0.3s;
    }
    
    .percentage {
      font-size: 14px;
      font-weight: 600;
      min-width: 50px;
      text-align: right;
    }
    
    .quota-details {
      display: flex;
      justify-content: space-between;
      font-size: 12px;
      color: var(--text-secondary, #888);
    }
    
    .mini-trend {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: 40px;
      opacity: 0.3;
      color: var(--primary, #3b82f6);
    }
    
    .mini-trend svg {
      width: 100%;
      height: 100%;
    }
    
    /* Ë∂®Âã¢ÂúñË°®ÂçÄ */
    .trend-section, .history-section, .alerts-section {
      background: var(--bg-secondary, #1a1a1a);
      border-radius: 16px;
      padding: 24px;
      margin-bottom: 24px;
    }
    
    .section-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 20px;
    }
    
    .section-header h2 {
      font-size: 18px;
      font-weight: 600;
      margin: 0;
    }
    
    .period-selector {
      display: flex;
      background: rgba(255, 255, 255, 0.05);
      border-radius: 8px;
      padding: 4px;
    }
    
    .period-selector button {
      padding: 6px 12px;
      background: transparent;
      border: none;
      border-radius: 6px;
      color: var(--text-secondary, #888);
      font-size: 13px;
      cursor: pointer;
      transition: all 0.2s;
    }
    
    .period-selector button.active {
      background: var(--primary, #3b82f6);
      color: white;
    }
    
    .chart-container {
      position: relative;
    }
    
    .chart-legend {
      display: flex;
      gap: 20px;
      margin-bottom: 16px;
    }
    
    .legend-item {
      display: flex;
      align-items: center;
      gap: 6px;
      font-size: 13px;
    }
    
    .legend-color {
      width: 12px;
      height: 12px;
      border-radius: 3px;
    }
    
    .chart-area {
      position: relative;
    }
    
    .trend-chart {
      width: 100%;
      height: 150px;
    }
    
    .trend-chart .grid line {
      stroke: rgba(255, 255, 255, 0.1);
      stroke-width: 1;
    }
    
    .data-point {
      opacity: 0;
      transition: opacity 0.2s;
    }
    
    .trend-chart:hover .data-point {
      opacity: 1;
    }
    
    .chart-labels {
      display: flex;
      justify-content: space-between;
      margin-top: 8px;
      font-size: 11px;
      color: var(--text-muted, #666);
    }
    
    /* Ê≠∑Âè≤Ë®òÈåÑË°®Ê†º */
    .view-all-btn, .clear-btn {
      padding: 6px 12px;
      background: rgba(255, 255, 255, 0.1);
      border: none;
      border-radius: 6px;
      color: var(--text-secondary, #888);
      font-size: 13px;
      cursor: pointer;
    }
    
    .history-table {
      border: 1px solid var(--border-color, #333);
      border-radius: 12px;
      overflow: hidden;
    }
    
    .table-header {
      display: grid;
      grid-template-columns: 100px 1fr 80px 80px 80px;
      padding: 12px 16px;
      background: rgba(255, 255, 255, 0.05);
      font-size: 12px;
      font-weight: 600;
      color: var(--text-secondary, #888);
    }
    
    .table-row {
      display: grid;
      grid-template-columns: 100px 1fr 80px 80px 80px;
      padding: 12px 16px;
      border-top: 1px solid var(--border-color, #333);
      font-size: 13px;
      transition: background 0.2s;
    }
    
    .table-row:hover {
      background: rgba(255, 255, 255, 0.05);
    }
    
    .table-row .type {
      display: flex;
      align-items: center;
      gap: 8px;
    }
    
    .table-row .percentage.warning {
      color: #f59e0b;
    }
    
    .empty-history {
      padding: 32px;
      text-align: center;
      color: var(--text-secondary, #888);
    }
    
    /* ÂëäË≠¶ÂàóË°® */
    .alerts-list {
      display: flex;
      flex-direction: column;
      gap: 12px;
    }
    
    .alert-item {
      display: flex;
      align-items: center;
      gap: 12px;
      padding: 16px;
      background: rgba(245, 158, 11, 0.1);
      border-radius: 12px;
      transition: opacity 0.2s;
    }
    
    .alert-item.acknowledged {
      opacity: 0.5;
    }
    
    .alert-icon {
      font-size: 24px;
    }
    
    .alert-content {
      flex: 1;
    }
    
    .alert-type {
      display: block;
      font-weight: 600;
      font-size: 14px;
    }
    
    .alert-message {
      font-size: 13px;
      color: var(--text-secondary, #888);
    }
    
    .ack-btn {
      padding: 6px 12px;
      background: rgba(255, 255, 255, 0.1);
      border: none;
      border-radius: 6px;
      color: white;
      font-size: 12px;
      cursor: pointer;
    }
    
    /* ÂçáÁ¥öÂºïÂ∞é */
    .upgrade-section {
      background: linear-gradient(135deg, rgba(139, 92, 246, 0.1), rgba(59, 130, 246, 0.1));
      border: 1px solid rgba(139, 92, 246, 0.3);
      border-radius: 16px;
      padding: 24px;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    
    .upgrade-content {
      display: flex;
      align-items: center;
      gap: 20px;
    }
    
    .upgrade-icon {
      font-size: 48px;
    }
    
    .upgrade-text h3 {
      margin: 0 0 4px;
      font-size: 18px;
    }
    
    .upgrade-text p {
      margin: 0;
      font-size: 14px;
      color: var(--text-secondary, #888);
    }
    
    .upgrade-features {
      display: flex;
      flex-wrap: wrap;
      gap: 12px;
    }
    
    .upgrade-features .feature {
      font-size: 13px;
      color: #22c55e;
    }
    
    .upgrade-cta {
      display: flex;
      align-items: center;
      gap: 8px;
      padding: 14px 28px;
      background: linear-gradient(135deg, #8b5cf6, #6366f1);
      border: none;
      border-radius: 12px;
      color: white;
      font-size: 16px;
      font-weight: 600;
      cursor: pointer;
      transition: all 0.2s;
    }
    
    .upgrade-cta:hover {
      transform: translateY(-2px);
      box-shadow: 0 8px 20px rgba(139, 92, 246, 0.4);
    }
    
    .upgrade-cta .arrow {
      transition: transform 0.2s;
    }
    
    .upgrade-cta:hover .arrow {
      transform: translateX(4px);
    }
    
    @media (max-width: 768px) {
      .page-header {
        flex-direction: column;
        align-items: flex-start;
        gap: 16px;
      }
      
      .quota-grid {
        grid-template-columns: 1fr;
      }
      
      .table-header, .table-row {
        grid-template-columns: 80px 1fr 60px 60px 60px;
        font-size: 12px;
      }
      
      .upgrade-section {
        flex-direction: column;
        text-align: center;
        gap: 20px;
      }
      
      .upgrade-content {
        flex-direction: column;
      }
    }
  `]
})
export class QuotaDashboardViewComponent implements OnInit, OnDestroy {
  public quotaService = inject(QuotaService);
  private authService = inject(AuthService);
  private router = inject(Router);
  private ipc = inject(ElectronIpcService);
  
  // ÁãÄÊÖã
  selectedPeriod = signal<'7d' | '30d' | '90d'>('7d');
  showAllHistory = false;
  
  private refreshInterval: any;
  private countdownInterval: any;
  
  // Ë∂®Âã¢Êï∏Êìö
  private _trendData = signal<TrendData | null>(null);
  trendData = this._trendData.asReadonly();
  
  // Ê≠∑Âè≤Êï∏Êìö
  private _history = signal<QuotaHistoryItem[]>([]);
  
  // ÂÄíË®àÊôÇ
  private _nextResetTime = signal<string | null>(null);
  nextResetTime = this._nextResetTime.asReadonly();
  
  // ÈÄ±ÊúüÈÅ∏È†Ö
  periods = [
    { value: '7d' as const, label: '7Â§©' },
    { value: '30d' as const, label: '30Â§©' },
    { value: '90d' as const, label: '90Â§©' }
  ];
  
  // ÂçáÁ¥öÂäüËÉΩÂàóË°®
  upgradeFeatures = [
    'Êõ¥Â§ö TG Â∏≥Ëôü',
    'Êõ¥È´òÊ∂àÊÅØÈÖçÈ°ç',
    'ÁÑ°Èôê AI Ë™øÁî®',
    'È´òÁ¥öÂäüËÉΩ'
  ];
  
  // ÂúñÊ®ôÊò†Â∞Ñ
  private quotaIcons: Record<string, string> = {
    daily_messages: 'üí¨',
    ai_calls: 'ü§ñ',
    tg_accounts: 'üì±',
    groups: 'üë•',
    devices: 'üíª',
    keyword_sets: 'üîë',
    auto_reply_rules: 'üîÑ',
    scheduled_tasks: '‚è∞',
  };
  
  // Á≠âÁ¥öÈÖçÁΩÆ
  private tierConfig: Record<string, { icon: string; gradient: string; name: string }> = {
    bronze: { icon: 'ü•â', gradient: 'linear-gradient(135deg, #CD7F32, #8B4513)', name: 'ÈùíÈäÖÊà∞Â£´' },
    silver: { icon: 'ü•à', gradient: 'linear-gradient(135deg, #C0C0C0, #808080)', name: 'ÁôΩÈäÄË°õÂ£´' },
    gold: { icon: 'ü•á', gradient: 'linear-gradient(135deg, #FFD700, #FFA500)', name: 'ÈªÉÈáëÁçµÊâã' },
    diamond: { icon: 'üíé', gradient: 'linear-gradient(135deg, #00CED1, #4169E1)', name: 'ÈëΩÁü≥ÁéãËÄÖ' },
    star: { icon: '‚≠ê', gradient: 'linear-gradient(135deg, #9B59B6, #8E44AD)', name: 'ÊòüËÄÄÂÇ≥Â•á' },
    king: { icon: 'üëë', gradient: 'linear-gradient(135deg, #FF6B6B, #EE5A24)', name: 'ÁéãËÄÖËá≥Â∞ä' },
  };

  ngOnInit() {
    this.loadData();
    this.startRefreshInterval();
    this.startCountdown();
  }

  ngOnDestroy() {
    if (this.refreshInterval) clearInterval(this.refreshInterval);
    if (this.countdownInterval) clearInterval(this.countdownInterval);
  }

  async loadData() {
    await Promise.all([
      this.quotaService.loadQuotaSummary(),
      this.quotaService.loadAlerts(),
      this.quotaService.loadMembershipLevels(),
      this.loadTrendData(),
      this.loadHistory(),
    ]);
  }

  private startRefreshInterval() {
    this.refreshInterval = setInterval(() => {
      this.quotaService.loadQuotaSummary();
    }, 60000); // ÊØèÂàÜÈêòÂà∑Êñ∞
  }

  private startCountdown() {
    this.updateCountdown();
    this.countdownInterval = setInterval(() => {
      this.updateCountdown();
    }, 1000);
  }

  private updateCountdown() {
    const now = new Date();
    const tomorrow = new Date(now);
    tomorrow.setDate(tomorrow.getDate() + 1);
    tomorrow.setHours(0, 0, 0, 0);
    
    const diff = tomorrow.getTime() - now.getTime();
    const hours = Math.floor(diff / (1000 * 60 * 60));
    const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
    const seconds = Math.floor((diff % (1000 * 60)) / 1000);
    
    this._nextResetTime.set(`${hours.toString().padStart(2, '0')}:${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`);
  }

  private async loadTrendData() {
    // Ê®°Êì¨Ë∂®Âã¢Êï∏ÊìöÔºàÂØ¶ÈöõÊáâÂæû API Áç≤ÂèñÔºâ
    const days = this.selectedPeriod() === '7d' ? 7 : this.selectedPeriod() === '30d' ? 30 : 90;
    const labels: string[] = [];
    const messagesData: number[] = [];
    const aiData: number[] = [];
    
    for (let i = days - 1; i >= 0; i--) {
      const date = new Date();
      date.setDate(date.getDate() - i);
      labels.push(`${date.getMonth() + 1}/${date.getDate()}`);
      messagesData.push(Math.floor(Math.random() * 100) + 20);
      aiData.push(Math.floor(Math.random() * 50) + 5);
    }
    
    this._trendData.set({
      labels,
      datasets: [
        { name: 'ÊØèÊó•Ê∂àÊÅØ', data: messagesData, color: '#3b82f6' },
        { name: 'AI Ë™øÁî®', data: aiData, color: '#8b5cf6' },
      ]
    });
  }

  private async loadHistory() {
    // Ê®°Êì¨Ê≠∑Âè≤Êï∏Êìö
    const history: QuotaHistoryItem[] = [];
    for (let i = 0; i < 10; i++) {
      const date = new Date();
      date.setDate(date.getDate() - i);
      history.push({
        date: `${date.getMonth() + 1}/${date.getDate()}`,
        quota_type: i % 2 === 0 ? 'daily_messages' : 'ai_calls',
        used: Math.floor(Math.random() * 80) + 20,
        limit: 100,
      });
    }
    this._history.set(history);
  }

  // Ë®àÁÆóÂ±¨ÊÄß
  tierIcon = computed(() => {
    const tier = this.authService.subscriptionTier() || 'bronze';
    return this.tierConfig[tier]?.icon || 'ü•â';
  });

  tierGradient = computed(() => {
    const tier = this.authService.subscriptionTier() || 'bronze';
    return this.tierConfig[tier]?.gradient || this.tierConfig.bronze.gradient;
  });

  tierName = computed(() => {
    const tier = this.authService.subscriptionTier() || 'bronze';
    return this.tierConfig[tier]?.name || 'ÈùíÈäÖÊà∞Â£´';
  });

  displayQuotas = computed(() => {
    const summary = this.quotaService.quotaSummary();
    if (!summary?.quotas) return [];
    
    return Object.entries(summary.quotas).map(([type, info]) => ({
      type,
      ...info as QuotaInfo,
    }));
  });

  displayHistory = computed(() => {
    const history = this._history();
    return this.showAllHistory ? history : history.slice(0, 5);
  });

  maxTrendValue = computed(() => {
    const trend = this._trendData();
    if (!trend) return 100;
    
    let max = 0;
    trend.datasets.forEach(ds => {
      ds.data.forEach(v => {
        if (v > max) max = v;
      });
    });
    return max || 100;
  });

  // ËºîÂä©ÊñπÊ≥ï
  getQuotaIcon(type: string): string {
    return this.quotaIcons[type] || 'üìä';
  }

  getStatusColor(status: string): string {
    const colors: Record<string, string> = {
      ok: '#22c55e',
      warning: '#f59e0b',
      critical: '#ef4444',
      exceeded: '#dc2626',
      unlimited: '#8b5cf6',
    };
    return colors[status] || '#888';
  }

  getStatusText(status: string): string {
    const texts: Record<string, string> = {
      ok: 'Ê≠£Â∏∏',
      warning: 'Âç≥Â∞áÈÅîÈôê',
      critical: 'Êé•Ëøë‰∏äÈôê',
      exceeded: 'Â∑≤Ë∂ÖÈôê',
      unlimited: 'ÁÑ°ÈôêÂà∂',
    };
    return texts[status] || '';
  }

  getProgressGradient(status: string): string {
    if (status === 'exceeded') return '#ef4444';
    if (status === 'critical' || status === 'warning') return 'linear-gradient(90deg, #f59e0b, #ef4444)';
    return 'linear-gradient(90deg, #3b82f6, #8b5cf6)';
  }

  formatNumber(num: number): string {
    if (num === -1) return '‚àû';
    if (num >= 1000000) return `${(num / 1000000).toFixed(1)}M`;
    if (num >= 1000) return `${(num / 1000).toFixed(1)}K`;
    return num.toString();
  }

  formatResetTime(isoTime: string): string {
    try {
      const date = new Date(isoTime);
      return date.toLocaleTimeString('zh-TW', { hour: '2-digit', minute: '2-digit' });
    } catch {
      return '';
    }
  }

  getTrendData(quotaType: string): string | null {
    // ÁîüÊàêÁ∞°ÂåñÁöÑ mini Ë∂®Âã¢Á∑ö
    const points: string[] = [];
    for (let i = 0; i < 7; i++) {
      const x = (i / 6) * 100;
      const y = 30 - Math.random() * 25;
      points.push(`${x},${y}`);
    }
    return points.join(' ');
  }

  generateChartPoints(data: number[]): string {
    if (!data || data.length === 0) return '';
    
    const max = this.maxTrendValue();
    const points: string[] = [];
    
    data.forEach((value, i) => {
      const x = (i / (data.length - 1 || 1)) * 400;
      const y = 150 - (value / max) * 150;
      points.push(`${x},${y}`);
    });
    
    return points.join(' ');
  }

  getHistoryPercentage(item: QuotaHistoryItem): number {
    if (item.limit === 0) return 0;
    return (item.used / item.limit) * 100;
  }

  showUpgradeHint(): boolean {
    const tier = this.authService.subscriptionTier();
    return tier !== 'king' && tier !== 'star' && tier !== 'diamond';
  }

  async acknowledgeAlert(alertId: number) {
    await this.quotaService.acknowledgeAlert(alertId);
  }

  async clearAlerts() {
    const alerts = this.quotaService.alerts();
    for (const alert of alerts) {
      if (!alert.acknowledged) {
        await this.quotaService.acknowledgeAlert(alert.id);
      }
    }
  }

  goToUpgrade() {
    this.router.navigate(['/upgrade']);
  }
}
